<template>
  <div class="re-switch">
    <div class="re-switch-container" @click="toggle($event)">
      <div class="re-switch-thumb" :style="styles">
        <input type="checkbox" :name="name" :id="id" :disabled="disabled" :value="value" tabindex="-1">
        <button :type="type" class="re-switch-holder"></button>
      </div>
    </div>

    <label :for="id || name" class="re-switch-label" v-if="$slots.default">
      <slot></slot>
    </label>
  </div>
</template>

<script>
  const checkedPosition = 75
  const initialPosition = '-1px'

  export default {
    name: 're-switch',
    props: {
      name: String,
      value: Boolean,
      id: String,
      disabled: Boolean,
      type: {
        type: String,
        default: 'button'
      }
    },
    data () {
      return {
        leftPos: initialPosition,
        checked: Boolean(this.value)
      }
    },
    computed: {
      classes () {
        return {
          're-checked': this.checked,
          're-disabled': this.disabled
        }
      },
      styles () {
        return {
          transform: `translate3D(${this.leftPos}, -50%, 0)`
        }
      }
    },
    watch: {
      checked () {
        this.setPosition()
      },
      value (value) {
        this.changeState(value)
      }
    },
    methods: {
      setPosition () {
        this.leftPos = this.checked ? checkedPosition + '%' : initialPosition
      },
      changeState (checked, $event) {
        if (typeof $event !== 'undefined') {
          this.$emit('change', checked, $event)

          if (!$event.defaultPrevented) {
            this.checked = checked
          }
          this.$emit('input', this.checked, $event)
        } else {
          this.checked = checked
        }
      },
      toggle ($event) {
        if (!this.disabled) {
          this.changeState(!this.checked, $event)
        }
      }
    },
    mounted () {
      this.$nextTick(this.setPosition)
    }
  }
</script>
<style lang="scss" scoped>
  @import '../../../assets/scss/components/switch.scss';
</style>



