<template>
  <div class="main">
    <div id='exploreResults'>
      <ExploreResultsClassifier
        :results="results"
        view="explore"
        @queryChanged="onQueryChanged" >
      </ExploreResultsClassifier>
    </div>
  </div>
</template>

<script>
import ExploreResultsClassifier from '@/components/classifier/ExploreResults'
import elasticsearch from './elasticsearch'

export default {
  name: 'biome',
  events: {},
  data: () => ({
    esResults: {
      aggregations: {},
      hits: {
        hits: [],
        total: 0
      }
    }
  }),
  computed: {
    results () {
      return {
        aggregations: this.esResults.aggregations,
        items: this.esResults.hits.hits,
        total: this.esResults.hits.total
      }
    }
  },
  methods: {
    onQueryChanged (query) {
      elasticsearch.search(query)
        .then(results => {
          this.esResults = results
        })
    }
  },
  components: {
    ExploreResultsClassifier
  }
}
</script>
<style lang="scss">
@import "src/assets/scss/pages/actions.scss";
@import "src/assets/scss/pages/empty.scss";
.main {
  padding: 2em 20px;
  position: relative;
  display: block;
  max-width: 1200px;
  margin: auto;
  height: 100%;
  min-height: calc(100vh - 70px);
}
</style>



