<template>
    <div class="results">
        <re-list :sorted="true" class="list list--explore">
            <!-- list header --> 
            <li class="list--header">
                <div class="list__item" v-if="!isFeedbackView">
                    <p v-if="hasGold"><button @click="sort('gold')" v-bind:class="[currentSortDir, {'active' : currentSort === 'gold'}]">Gold</button></p>
                    <p v-if="hasGold"><button @click="sort('predicted')" v-bind:class="[currentSortDir, {'active' : currentSort === 'predicted'}]">Predicted</button></p>
                    <p v-if="hasGold"><button @click="sort('confidence')" v-bind:class="[currentSortDir, {'active' : currentSort === 'confidence'}]">Confidence</button></p> 
                </div>
                <div class="global-actions" v-else>
                    <re-button class="global-actions__button button-clear"  @click="changeAllFeedbackStatus('validated')">Validate all<svgicon name="check"></svgicon></re-button>
                    <re-button class="global-actions__button button-clear"  @click="changeAllFeedbackStatus('ignored')">Ignore all<svgicon name="ignore"></svgicon></re-button>
                </div>
            </li>
            <re-pagination v-if="getTotal != 0" :total-pages="totalPages" :total="total" :per-page="perPage" :current-page="currentPage" @pagechanged="onPageChange"/>
            <transition-group name="flip-list">
            <li v-for="(record, index) in records" :key="index" v-bind:class="{inactive : !record.activerecord, feedback: isFeedbackView }">
                <div class="list__item">

                    <div class="list__item--highlighted" v-if="!isFeedbackView">
                        <p v-if="hasGold" class="gold">{{record.gold}}</p>
                        <p v-if="hasGold" class="predicted"><span :class="{ko : !record.predictedOk}">{{record.predicted}}</span></p>
                        <p v-if="hasGold"><re-numeric :value="decorateConfidence(record.confidence)" :decimals="2">%</re-numeric></p>
                    </div>
                    <div class="feedback-interactions" v-else>
                        <div v-for="(key, index) in getSortedClasses(record.classes).slice(0, 3)" :key="index">
                            <re-toggle-radio class="clear --feedback validate" v-model="feedbackItems[record._id]" @change="updateFeedbackSingle(record._id, $event)" :reValue="key">
                                <div class="radio-data">
                                    <span class="hover" aria-hidden="true"><svgicon name="check"></svgicon></span>
                                    <span class="data"><span class="radio-data__text">{{key}}</span><re-numeric :value="decorateConfidence(record.classes[key])" :decimals="2">%</re-numeric></span>
                                </div>
                            </re-toggle-radio>
                        </div>
                        <re-toggle-radio class="clear --feedback ignore" v-model="feedbackItems[record._id]" @change="updateFeedbackSingle(record._id, $event)" reValue="ignored">
                                <div class="radio-data">
                                    <span class="hover" aria-hidden="true"><svgicon name="ignore"></svgicon></span>                            
                                    <span class="data">Ignore</span>
                                </div>
                        </re-toggle-radio>
                    </div>
                    <!-- <div v-if="pair">
                        <span class="text"><em>Hypothese  </em>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Dolorem quas suscipit laudantium ullam saepe consectetur eius voluptatibus nostrum pariatur ipsa deleniti voluptas autem ad, voluptatem dolor.</span>
                        <span class="text"><em>Premise  </em>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Dolorem quas suscipit laudantium ullam saepe consectetur eius voluptatibus nostrum pariatur ipsa deleniti voluptas autem ad, voluptatem dolor.</span>
                    </div> -->
                    <span class="text">{{record.name}}</span>
                </div>
                <re-dropdown class="dropdown--list list__toggledetail">
                    <div slot="dropdown-content">
                        <table-preview :previewData="record._source">
                        </table-preview>
                    </div>
                </re-dropdown>
            </li>
            </transition-group>
        </re-list>
    </div>
</template>
<script>
import '@/assets/iconsfont/check'
import '@/assets/iconsfont/ignore'
import reList from '@/components/elements/reList/reList'
import reButton from '@/components/elements/reButton/reButton'
import tablePreview from '@/components/datasources/TablePreview'
import reDropdown from '@/components/elements/reDropdown/reDropdown'
import reToggleRadio from '@/components/elements/reRadio/reToggleRadio'
import reNumeric from '@/components/elements/format/reNumeric/reNumeric'
import rePagination from '@/components/elements/rePagination/rePagination'

export default {
  name: 'search-results',
  data: () => ({
    currentSort: 'name',
    currentSortDir: 'asc',
    activeAllFeedback: undefined
  }),
  // TODO clean props and typify
  props: ['records', 'view', 'feedbackItems', 'total', 'currentPage', 'perPage', 'totalPages', 'getTotal'],
  computed: {
    hasGold () {
      return this.view === 'explore'
    },
    isFeedbackView () {
      return this.view === 'feedback'
    }
  },
  methods: {
    changeAllFeedbackStatus (status) {
      if (this.activeAllFeedback === undefined) {
        this.$emit('changeAllFeedbackStatus', status)
        this.activeAllFeedback = status
      } else {
        this.$emit('changeAllFeedbackStatus', undefined)
        this.activeAllFeedback = undefined
      }
    },
    onPageChange (page) {
      this.$emit('pagechanged', page)
    },
    decorateConfidence (confidence) {
      return confidence * 100
    },
    sort (s) {
      if (s === this.currentSort) {
        this.currentSortDir = this.currentSortDir === 'asc' ? 'desc' : 'asc'
      }
      this.currentSort = s
      this.$emit('sort', this.currentSort, this.currentSortDir)
    },
    getSortedClasses (classes) {
      let sorted = Object.keys(classes).sort((a, b) => { return classes[b] - classes[a] })
      return sorted
    },
    updateFeedbackSingle (key, value) {
      if (value !== this.feedbackItems[key]) {
        this.$emit('feedbackChanged', {
          id: key,
          oldFeedback: this.feedbackItems[key],
          newFeedback: value
        })
      }
    }
  },
  components: {
    tablePreview,
    reDropdown,
    reList,
    reToggleRadio,
    reButton,
    rePagination,
    reNumeric
  }
}
</script>
<style lang="scss" scoped>
@import 'src/assets/scss/pages/actions.scss';
@import 'src/assets/scss/pages/empty.scss';
@import 'src/assets/scss/pages/feedback.scss';
@import 'src/assets/scss/components/checkbox.scss';
.flip-list-move {
  transition: all .3s;
  opacity: 0.8;
}
.flip-list-move .list__item p {
  opacity: 0.5;
}
</style>



