<template>
  <aside class="sidebar">
    <transition name="fade" appear>
    <div v-if="view == 'explore'" >
      <div class="sidebar__content">
        <div class="progress__block">
          <p>Metrics %</p>
          <span v-for="(metric,key) in metricsOverall" :key="key">
            <label>{{metric.key}}</label><span class="progress">{{metric.value}}</span>
            <re-progress :class=metric.key re-mode="determinate" :progress=parseInt(metric.value)></re-progress>
          </span>
        </div>
      </div>
      <div class="sidebar__content" v-for="(entries,key) in metrics" :key="key">
        <div class="progress__block">
          <collapsable-progress-bars :entries=entries :entriesKey=key :barsNumber="3"></collapsable-progress-bars>
        </div>
      </div>
      <!-- logo -->
      <biome-isotipo :height=28 :width=50 class="biome-isotipo"></biome-isotipo>
    </div>
    </transition>
    <transition name="fade" appear>
    <div v-if="view == 'feedback'">
      <div class="sidebar__content">
        <label class="total">Total Feedback</label><span class="progress">{{getTotalFeedbackItems}}</span>
        <re-progress re-mode="determinate" :progress="getFeedbackProgress"></re-progress>
         <div class="info" v-for="(status, index) in feedbackCheckStatus" :key="index">
              <label>{{status.status}} </label>
              <span class="records-number"> {{status.count}}</span>
        </div>        
      </div>
      <re-button @click="explore()" class="button-secondary--bordered button-full" :loading=loading>
        <svgicon name="export" width="auto" height="14" color="#64B09F"></svgicon>
        Export batch as data source
      </re-button>
    </div>
    </transition>
  </aside>    
</template>
<script>
import reProgress from '@/components/elements/reProgress/reProgress'
import reButton from '@/components/elements/reButton/reButton'
import reCheckbox from '@/components/elements/reCheckbox/reCheckbox'
import biomeIsotipo from '@/components/elements/logos/biomeIsotipo'
import CollapsableProgressBars from './CollapsableProgressBars'
import '@/assets/iconsfont/export'

export default {
  name: 'sidebar',
  data: () => ({
    loading: false
  }),
  // TODO clean and typify
  props: ['metrics', 'view', 'feedbackMetrics', 'totalItems'],
  methods: {
    explore () {
      this.loading = true
    }
  },
  computed: {
    metricsOverall () {
      let metricsOveral = []
      Object.entries(this.metrics || {})
        .forEach(metricEntry => {
          let metricKey = metricEntry[0]
          let values = Object.values(metricEntry[1])
            .flatMap(value => Object.values(value).map(parseFloat))
          metricsOveral.push({
            key: metricKey,
            value: (values.reduce((sum, e) => e + sum, 0) / values.length).toFixed(2)
          })
        })
      return metricsOveral
    },
    feedbackItems () {
      return Object.values(this.feedbackMetrics).reduce((total, elem) => total + elem, 0)
    },
    getTotalFeedbackItems () {
      if (this.feedbackItems <= 0) {
        return '-'
      } else {
        return this.feedbackItems
      }
    },
    getFeedbackProgress () {
      if (this.feedbackItems <= 0) {
        return 0
      } else {
        let progress = (100 * this.feedbackItems) / (this.totalItems)
        return progress
      }
    },
    feedbackCheckStatus () {
      return [
        {status: 'Validated', count: this.feedbackMetrics.validated || 0},
        {status: 'Ignored', count: this.feedbackMetrics.ignored || 0},
        {status: 'Rejected', count: this.feedbackMetrics.rejected || 0}
      ]
    }
  },
  components: {
    reProgress,
    reButton,
    reCheckbox,
    biomeIsotipo,
    CollapsableProgressBars
  }
}
</script>
<style lang="scss" scoped>
@import 'src/assets/scss/components/sidebar.scss';
@import 'src/assets/scss/components/checkbox.scss';
.biome-isotipo {
  text-align: center
}
</style>



