<template>
  <div class="re-radio" :class="classes">
    <div class="radio-container" @click.stop="toggleCheck">
      <input type="radio" :name="name" :id="id" :disabled="disabled" :value="value">
    </div>

    <label :for="id || name" class="radio-label" v-if="$slots.default"  @click.prevent="toggleCheck($event)">
      <slot></slot>
    </label>
  </div>
</template>

<script>
  export default {
    name: 're-toggle-radio',
    props: {
      name: String,
      id: String,
      value: [String, Boolean, Number],
      reValue: {
        type: [String, Boolean, Number]
      },
      disabled: Boolean
    },
    computed: {
      classes () {
        return {
          'checked': typeof this.value !== 'undefined' && this.value !== null && this.reValue.toString() === this.value.toString(),
          'disabled': this.disabled
        }
      }
    },
    methods: {
      toggleCheck ($event) {
        if (!this.disabled) {
          if (this.value !== this.reValue) {
            this.$emit('change', this.reValue, $event)
            this.$emit('input', this.reValue, $event)
          } else {
            this.$emit('change', undefined, $event)
            this.$emit('input', undefined, $event)
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../../assets/scss/components/radio.scss'
</style>



