<template>
    <div>
        <p @click="sort('value')" :class="[currentSortDir, {'active' : currentSort === 'value'}, {'button' : sortableMetrics}]"> {{entriesKey}} %</p>
        <re-button class="button-icon" @click="toggleShowCollapse()" v-if="entries.length > barsNumber"><svgicon :name="numberOfEntries === undefined ? 'drop-up' : 'drop-down'"  width="12" height="auto"></svgicon></re-button>
        <span v-for="(entry, i) in sortedEntries.slice(0, numberOfEntries)" :class=entriesKey :key=i>
          <label>{{entry.name}}</label><span class="progress">{{entry.value}}</span>
          <re-progress re-mode="determinate" :progress="parseInt(entry.value)"></re-progress>
        </span>
    </div>
</template>

<script>
import reProgress from '@/components/elements/reProgress/reProgress'
import reButton from '@/components/elements/reButton/reButton'
export default {
  name: 'collapsable-progress-bars',
  props: ['entries', 'entriesKey', 'barsNumber'],
  data: () => ({
    numberOfEntries: '',
    currentSortDir: 'desc',
    currentSort: 'value'
  }),
  mounted () {
    this.numberOfEntries = this.barsNumber
  },
  methods: {
    toggleShowCollapse () {
      if (this.numberOfEntries === this.barsNumber) {
        this.numberOfEntries = undefined
      } else {
        this.numberOfEntries = this.barsNumber
      }
    },
    sort: function (s) {
      if (s === this.currentSort) {
        this.currentSortDir = this.currentSortDir === 'asc' ? 'desc' : 'asc'
      }
      this.currentSort = s
    }
  },
  computed: {
    entryMetrics () {
      let entries = []
      Object.entries(this.entries || {})
        .forEach(metricEntry => {
          let metricKey = Object.entries(metricEntry[1])
            .flatMap(value => Object.values(value))
          entries.push({
            name: metricKey[0],
            value: parseFloat(metricKey[1])
          })
        })
      return entries
    },
    sortedEntries () {
      return this.entryMetrics.sort((a, b) => {
        let modifier = 1
        if (this.currentSortDir === 'desc') modifier = -1
        if (a[this.currentSort] < b[this.currentSort]) return -1 * modifier
        if (a[this.currentSort] > b[this.currentSort]) return 1 * modifier
        return 0
      })
    },
    sortableMetrics () {
      if (this.entries.length > this.barsNumber && this.numberOfEntries === undefined) {
        return true
      }
    }
  },
  components: {
    reProgress,
    reButton
  }
}
</script>
<style lang="scss" scoped>
@import 'src/assets/scss/components/sidebar.scss';
</style>




