<template>
  <transition name="fade">
    <table class="table--preview">
        <tr v-for="row in Object.entries(previewData)" :key="row[0]">
          <td v-for="col in row" v-bind:key="col">{{col}}</td>
          <td v-if="showConnections" class="table__buttons">
            <re-toggle-radio class="clear --inline" v-for="value in connectOptions" 
              :key="value"  
              v-model="connectState[row[0]]" 
              :re-value=value>{{value}}
            </re-toggle-radio>
          </td>
          <td v-if="filtrable" class="table__filters">
            <re-button class="clear"><svgicon name="filter" width="14" height="auto" color="#787878"/></re-button>
          </td>
        </tr>
    </table>
  </transition>
</template>
<script>
  import reToggleRadio from '@/components/elements/reRadio/reToggleRadio'
  import reButton from '@/components/elements/reButton/reButton'
  import '../../assets/iconsfont/filter'
  export default {
    data: () => {
      return {
        connectState: {}
      }
    },
    name: 'table-preview',
    props: ['previewData', 'connectOptions', 'filtrable'],
    watch: {
      connectState (newValue) {
        this.$emit('connectionChanged', this.connectState)
      }
    },
    computed: {
      showConnections () {
        return this.connectOptions
      }
    },
    components: {
      reToggleRadio,
      reButton
    }
  }
</script>

<style lang="scss" scoped>
@import '../../assets/scss/components/table.scss';
.re-radio {
  &.checked {
    background: palette(green);
    color: palette(white);
    transition: all .02s ease-in-out;
  }
}
</style>


