<template>
  <div>
      <label :class="{'--active': confidenceExpanded}">{{filter.name}}</label>
      <div v-click-outside="onClickOutside" class="filter__item filter__item--confidence" v-bind:class="{expanded :confidenceExpanded}">
        <re-button class="clear" @click="expandConfidence()">
        </re-button>
        <span class="range">{{min}}% to {{max}}%</span>
        <vega-lite class="confidence" :data="filter.values" :autosize="autosize" :config="config" :mark="mark" :encoding="encoding"/>
        <div class="range__container">
          <span class="range--start">-</span><span class="range--end">+</span>
          <re-range ref="slider" v-if="confidenceExpanded" v-model="confidenceRanges" v-bind="rangeOptions"></re-range>
          <re-button class="button-clear--primary" @click="onApplyConfidenceRange">Apply</re-button>
        </div>
      </div>
      <div class="overlay" v-if="confidenceExpanded"></div>
  </div>
</template>

<script>
import Vue from 'vue'
import VueVega from 'vue-vega'
import reButton from '@/components/elements/reButton/reButton'
import reRange from '@/components/elements/reRange/reRange'

Vue.use(VueVega)

export default {
  name: 'FilterConfidence',
  props: ['filterName', 'data', 'filter'],
  methods: {
    expandConfidence: function () {
      this.confidenceExpanded = true
    },
    onApplyConfidenceRange () {
      this.$emit('apply', this.confidenceRanges)
      this.confidenceExpanded = false
    },
    onClickOutside (event) {
      this.confidenceExpanded = false
    }
  },
  components: {
    reButton,
    reRange
  },
  created () {
    this.confidenceRanges = [this.rangeOptions.min, this.rangeOptions.max]
  },
  computed: {
    min () {
      return this.confidenceRanges[0]
    },
    max () {
      return this.confidenceRanges[1]
    }
  },
  data: () => ({
    confidenceExpanded: false,
    rangeOptions: {
      height: 4,
      dotSize: 20,
      min: 0,
      max: 100,
      interval: 1,
      show: true
    },
    confidenceRanges: [],
    autosize: {
      type: 'none',
      resize: true,
      contains: 'padding'
    },
    mark: 'area',
    config: {
      mark: {
        color: '#D9D7E4',
        binSpacing: 0
      },
      bar: {
        binSpacing: 0,
        discreteBandSize: 0,
        continuousBandSize: 0
      },
      axis: {
        labels: false
      },
      view: {
        height: 100,
        width: 400
      }
    },
    encoding: {
      x: {field: 'key', type: 'ordinal', scale: {rangeStep: null}},
      y: {field: 'count', type: 'quantitative', aggregate: 'sum'}
    }
  })
}
</script>
<style lang="scss" scoped>
@import 'src/assets/scss/pages/actions.scss';
</style>




