import axios from 'axios'
import defaultData from './mockData'

let elasticsearch = axios.create({
  baseURL: process.env.ELASTICSEARCH
})

export default {
  async search (query) {
    try {
      let results = await elasticsearch.post('/', query)
      return results.data
    } catch (error) {
      return defaultData
    }
  }
}



