<template>
      <span>
        {{numericValue}}<slot></slot>
      </span>
</template>

<script>
  export default {
    name: 're-numeric',
    data: () => ({}),
    props: ['value', 'decimals'],
    computed: {
      numericValue () {
        return this.value.toFixed(this.decimals)
      }
    }
  }
</script>



