<template>
  <transition name="re-progress" appear>
    <div class="re-progress">
      <div class="re-progress-track" :style="styles"></div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: 're-progress',
    props: {
      indeterminate: Boolean,
      progress: {
        type: Number,
        default: 0
      }
    },
    computed: {
      classes () {
        return {
          're-indeterminate': this.indeterminate
        }
      },
      styles () {
        if (!this.indeterminate) {
          return {
            width: this.progress + '%'
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
@import '../../../assets/scss/components/progress.scss'
</style>



