<template>
  <div id='SearchFilterList' class="filters__container">
    <div class="filters">
      <div class="filter" v-if="isFilterAvailable(goldFilter)">
        <div>
          <select-filter
            :filter=goldFilter
            :fid="goldFilter.id"
            :options="goldFilter.values" 
            @filter-changed="onFilterChanged" 
            @remove="$emit('remove', goldFilter.id)" 
            @apply="onApplyOn(goldFilter.id)">
          </select-filter>
        </div>
      </div>
      <div class="filter" v-if="isFilterAvailable(predictedFilter)">
        <div>
          <select-filter
            :filter=predictedFilter
            :fid="predictedFilter.id"
            :options="predictedFilter.values" 
            @filter-changed="onFilterChanged" 
            @remove="$emit('remove', predictedFilter.id)" 
            @apply="onApplyOn(predictedFilter.id)">
          </select-filter>
        </div>
      </div>
      <div class="filter" v-if="isFilterAvailable(confusionMatrixFilter)">
        <filter-confusion-matrix 
          :filter=confusionMatrixFilter
          @apply="onFilterMatrixApply">
        </filter-confusion-matrix>
      </div>
      <div class="filter" v-if="isFilterAvailable(confidenceFilter)">
        <filter-confidence :class="{'--active' : filterActive(filtersStatus.confidence)}"
          :filter=confidenceFilter
          @apply="onFilterConfidenceApply">
        </filter-confidence>
      </div>
    </div>
  </div>
</template>
<script>
  import SelectFilter from './SelectFilter'
  import FilterConfusionMatrix from './FilterConfusionMatrix'
  import FilterConfidence from './FilterConfidence'
  export default {
    name: 'FilterList',
    props: {
      filtersStatus: Object,
      filters: Object
    },
    data: () => ({
      filters_copy: {
        gold: {
          id: 'gold',
          values: []
        },
        predicted: {
          id: 'predicted',
          values: []
        },
        confidence: {
          id: 'confidence'
        }
      }
    }),
    watch: {
      filters (newValue) {
        this.filters_copy = newValue
      }
    },
    computed: {
      goldFilter () {
        return this.filters.gold
      },
      predictedFilter () {
        return this.filters.predicted
      },
      confidenceFilter () {
        return this.filters.confidence
      },
      confusionMatrixFilter () {
        return this.filters.confusionMatrix
      }
    },
    methods: {
      isFilterAvailable (filter) {
        return filter && filter.values
      },
      onFilterChanged (filterId, valueId, isActive) {
        let value = this.filters[filterId].values[valueId]
        value.selected = isActive
        this.filters_copy[filterId].values[valueId] = value
      },
      onFilterMatrixApply (selected) {
        Object.entries(selected).forEach(entry => {
          let filterId = entry[0]
          let filterValue = entry[1]
          let filterValueIdx = this.filters_copy[filterId].values.findIndex(value => value.name === filterValue)
          this.onFilterChanged(filterId, filterValueIdx, true)
        })
        this.onApplyOn()
      },
      onFilterConfidenceApply (ranges) {
        this.$emit('filterConfidenceApply', this.filters.confidence.id, ranges)
      },
      onApplyOn () {
        this.$emit('apply', this.filters_copy)
      },
      filterActive (filter) {
        return filter && filter.length > 0
      }
    },
    components: {
      FilterConfusionMatrix,
      FilterConfidence,
      SelectFilter
    }
  }
</script>
<style lang="scss" scoped>
  @import 'src/assets/scss/pages/actions.scss';
  @import 'src/assets/scss/components/select.scss';
</style>



