<template>
  <div class="pagination">

  <p class="pagination__text">
    <strong>{{fromItem}} - {{toItem}}</strong> <span>of</span> {{total}}
  </p>

  <ul>

    <li>
      <button class="pagination__button"
        type="button"
        @click="onClickPreviousPage"
        :disabled="isInFirstPage"
        aria-label="Go to previous page">
        <span class="pagination__check prev"></span>
      </button>
    </li>

    <li>
      <button class="pagination__button"
        type="button"
        @click="onClickNextPage"
        :disabled="isInLastPage"
        aria-label="Go to next page">
        <span class="pagination__check next"></span>
      </button>
    </li>
  </ul>
  </div>
</template>
<script>
  export default {
    name: 're-pagination',
    props: {
      maxVisibleButtons: {
        type: Number,
        required: false,
        default: 3
      },
      totalPages: {
        type: Number,
        required: true
      },
      total: {
        type: Number,
        required: true
      },
      perPage: {
        type: Number,
        required: true
      },
      currentPage: {
        type: Number,
        required: true
      }
    },
    computed: {
      fromItem () {
        return (this.currentPage - 1) * this.perPage + 1
      },
      toItem () {
        if (this.totalPages !== 1) {
          return ((this.currentPage - 1) * this.perPage + 1 + (this.perPage)) - 1
        } else {
          return this.total
        }
      },
      startPage () {
        if (this.currentPage === 1) {
          return 1
        }
        if (this.currentPage === this.totalPages) {
          return this.totalPages - this.maxVisibleButtons + 1
        }
        return this.currentPage - 1
      },
      endPage () {
        return Math.min(this.startPage + this.maxVisibleButtons - 1, this.totalPages)
      },
      pages () {
        const range = []
        for (let i = this.startPage; i <= this.endPage; i += 1) {
          range.push({
            name: i,
            isDisabled: i === this.currentPage
          })
        }
        return range
      },
      isInFirstPage () {
        return this.currentPage === 1
      },
      isInLastPage () {
        return this.currentPage === this.totalPages
      }
    },
    methods: {
      onClickFirstPage () {
        this.$emit('pagechanged', 1)
      },
      onClickPreviousPage () {
        this.$emit('pagechanged', this.currentPage - 1)
      },
      onClickPage (page) {
        this.$emit('pagechanged', page)
      },
      onClickNextPage () {
        this.$emit('pagechanged', this.currentPage + 1)
      },
      onClickLastPage () {
        this.$emit('pagechanged', this.totalPages)
      },
      isPageActive (page) {
        return this.currentPage === page
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../../assets/scss/components/pagination.scss'
</style>


