let defaultMappingConfig = {
  queryField: 'input.tokens',
  predictedField: 'annotation.max_class.keyword',
  goldField: 'input.gold_label.keyword',
  confidenceField: 'annotation.max_class_prob',
  feedbackStatus: 'feedback.status.keyword'
}

let defaultConfiguration = (mappingConfig, enableGold) => {
  let ranges = (from, to, interval) => {
    let range = (from, to, step) =>
      Array(~~((to - from) / step) + 1) // '~~' is Alternative for Math.floor()
      .fill().map((v, i) => from + i * step)
    let zip = rows => rows[0].map((_, c) => rows.map(row => row[c]))
    return zip([range(from, to - interval, interval), range(from + interval, to, interval)])
    .map(ranges => ({
      from: ranges[0],
      to: ranges[1]
    }))
  }
  let defaultOptions = {
    _source: true,
    inputField: mappingConfig.queryField,
    queryType: 'match',
    queryAggs: {
      predicted: {
        terms: {
          field: mappingConfig.predictedField,
          size: 100,
          order: {
            _count: 'desc'
          }
        }
      },
      confidence_range: {
        range: {
          field: mappingConfig.confidenceField,
          ranges: ranges(0.0, 1.0, 0.05)
        }
      }
    }
  }
  if (enableGold) {
    defaultOptions.queryAggs.gold = {
      terms: {
        field: mappingConfig.goldField,
        size: 100,
        order: {
          _count: 'desc'
        }
      }
    }
    defaultOptions.queryAggs.confusion_matrix = {
      terms: {
        field: mappingConfig.goldField,
        size: 100
      },
      aggs: {
        predicted: {
          terms: {
            field: mappingConfig.predictedField,
            size: 100
          }
        }
      }
    }
  }
  return defaultOptions
}
function getFilterRange (filtersStatus = {}, searchFieldsMap = {}) {
  let confidence = filtersStatus['confidence'][0]
  let min = confidence[0] * 0.01
  let max = confidence[1] * 0.01
  let filterRange = {range: {}}
  filterRange.range[searchFieldsMap.confidenceField] = {
    gte: min,
    lte: max
  }
  return filterRange
}

function configureSort (sortBy, sortOrder, mappingConfig) {
  let sort = []

  if (sortBy !== undefined) {
    let sortByConfig = {}
    let sortByField = mappingConfig[sortBy] || mappingConfig[sortBy + 'Field']

    sortByConfig[sortByField] = {order: sortOrder}
    sort.push(sortByConfig)
  }

  sort.push({_score: {order: 'desc'}})
  sort.push({_id: {order: 'asc'}})

  return sort
}

class ElasticManager {
  feedbackResultsQuery () {
    return {
      size: 0,
      from: 0,
      aggs: {
        feedback: {
          terms: {
            field: defaultMappingConfig.feedbackStatus,
            size: 100,
            order: {
              _count: 'desc'
            }
          }
        }
      }
    }
  }

  toESQuery ({
    keyword,
    filters = [],
    filtersStatus = {},
    esOptions = {},
    mappingConfig = defaultMappingConfig,
    sortBy, sortOrder
  }, hasGold) {
    const searchOptions = {
      filterTerms: filters.map(filter => {
        if (filter.id === 'confidence') {
          return getFilterRange(filtersStatus, mappingConfig)
        } else {
          let termId = mappingConfig[filter.id] || mappingConfig[filter.id + 'Field']
          let termFilter = {}
          termFilter[termId] = filtersStatus[filter.id]
          return {
            terms: termFilter
          }
        }
      }),
      ...defaultConfiguration(mappingConfig, hasGold),
      ...esOptions
    }
    let query = {}
    let queryMatch = {}
    if (keyword.length <= 0) {
      queryMatch = {match_all: {}}
    } else {
      queryMatch.match = {}
      queryMatch.match[searchOptions.inputField] = {
        query: keyword,
        boost: 20
      }
    }
    if (this.filterTermsEnabled(searchOptions)) {
      query.bool = {
        minimum_should_match: searchOptions.filterTerms.length,
        must: queryMatch,
        should: [searchOptions.filterTerms]
      }
    } else {
      query = queryMatch
    }

    return {
      query: query,
      _source: searchOptions._source,
      aggs: searchOptions.queryAggs,
      from: searchOptions.from,
      size: searchOptions.size,
      sort: configureSort(sortBy, sortOrder, mappingConfig)
    }
  }
  filterTermsEnabled (searchOptions) {
    return searchOptions.filterTerms && searchOptions.filterTerms.length > 0
  }
}
let manager = new ElasticManager()
export default manager



