<template>
    <div>
      <label :class="{'--active': confusionExpanded}">{{filter.name}}</label>
      <div v-click-outside="onClickOutside" class="filter__item filter__item--confusion" v-bind:class="{expanded :confusionExpanded}">
        <re-button class="clear" @click="expandConfusion()"></re-button>
        <div class="datum label-default" v-if="hoverActive">
          <span>{{this.hover.count}} records <strong>{{this.hover.gold}}</strong> predicted as <strong>{{this.hover.predicted}}</strong></span>
        </div>
        <vega-lite @signal:pointer_tuple="updateSelection" @signal:hover_tuple="updateDatum" class="confusion-matrix" :autosize="autosize" :config="matrixConfig" :data="filter.values" :layer="matrixEncoding"/>
        <re-button class="button-clear--primary" @click="onApplyConfusion">Apply</re-button>
      </div>
      <div class="overlay" v-if="confusionExpanded"></div>
    </div>
</template>
<script>
import reButton from '@/components/elements/reButton/reButton'
import Vega from 'vue-vega'

export default {
  name: 'FilterConfusionMatrix',
  props: ['filter'],
  components: {
    reButton,
    Vega
  },
  methods: {
    updateSelection (tuple) {
      tuple.fields.forEach((key, i) => {
        this.selected[key] = tuple.values[i]
      })
    },
    updateDatum (tuple) {
      tuple.fields.forEach((key, i) => {
        this.hover[key] = tuple.values[i]
      })
      this.hoverActive = true
    },
    expandConfusion () {
      this.confusionExpanded = true
    },
    onApplyConfusion () {
      this.$emit('apply', this.selected)
      this.confusionExpanded = false
    },
    onClickOutside (event) {
      this.confusionExpanded = false
    }
  },
  watch: {
  },
  data: () => ({
    confusionExpanded: false,
    hoverActive: false,
    selected: {
      gold: '',
      predicted: ''
    },
    hover: {
      gold: '',
      predicted: '',
      count: ''
    },
    autosize: {
      type: 'pad',
      resize: true
    },
    matrixConfig: {
      view: {
        height: 360,
        width: 540
      },
      axis: {
        labelLimit: 200,
        labelFontSize: 18,
        titleFontSize: 22,
        labelPadding: 50,
        titlePadding: 80
      },
      mark: {
        color: '#8777D9'
      },
      scale: {bandPaddingInner: 0, bandPaddingOuter: 0}
    },
    matrixEncoding: [{
      mark: 'rect',
      encoding: {
        y: {
          field: 'gold',
          type: 'nominal',
          scale: {rangeStep: null}
        },
        x: {
          field: 'predicted',
          type: 'nominal',
          scale: {rangeStep: null}
        },
        tooltip: {field: 'count', type: 'quantitative'},
        color: {
          condition: {
            selection: {not: 'pointer'},
            aggregate: 'sum',
            field: 'count',
            type: 'quantitative',
            scale: {range: ['#d6d3ea', '#8777D9', '#3B3269'], type: 'linear'}
          },
          value: '#84B5AF'
        }
      },
      selection: {
        pointer: {
          type: 'single',
          on: 'click',
          fields: ['gold', 'predicted']
        },
        hover: {
          type: 'single',
          on: 'rect:mouseover',
          fields: ['gold', 'predicted', 'count']
        }
      },
      legend: null
    }]
  })
}
</script>
<style lang="scss" scoped>
@import 'src/assets/scss/pages/actions.scss';
.datum {
  margin-bottom: 20px;
  span {
    display: block;
  }
}
</style>





