<template>
  <div>
    <label :class="{'--active': visible}">{{filter.name}}</label>
    <re-filter-dropdown :class="{'highlighted' : visible, 'dropdown--active' : removeFilterActive}" class="dropdown--filter" :visible=visible @visibility=onVisibility>
      <span slot="dropdown-header">
        <span class="dropdown__selectables" v-for="option,key in options.slice(0, 2)" :key="option.id">
        {{option.name}}
        </span>
        <span class="dropdown__placeholder">Select option</span>
      </span>
      <div slot="dropdown-content">
        <ul>
          <li v-for="option,key in options" :key="option.id">
          <re-checkbox :value="option.selected" @change="onFilterChanged($event,key)">{{option.name}}</re-checkbox>
          </li>
        </ul>
        <re-button class="button-clear" @click=onApply>Apply</re-button>
      </div>
    </re-filter-dropdown>
    <div class="overlay" v-if="visible"></div>
  </div>
</template>

<script>
  import reButton from '@/components/elements/reButton/reButton'
  import reFilterDropdown from '@/components/elements/reDropdown/reFilterDropdown'
  import reCheckbox from '@/components/elements/reCheckbox/reCheckbox'

  export default {
    name: 'select-filter',
    props: ['options', 'fid', 'removeFilterActive', 'filter'],
    data: () => ({
      visible: false
    }),
    methods: {
      onFilterChanged (value, id) {
        this.$emit('filter-changed', this.fid, id, value)
      },
      onVisibility (value) {
        this.visible = value
      },
      onApply () {
        this.$emit('apply')
        this.visible = false
      }
    },
    components: {
      reButton,
      reFilterDropdown,
      reCheckbox
    }
  }
</script>

<style lang="scss" scoped>
@import 'src/assets/scss/components/dropdown.scss';
@import 'src/assets/scss/pages/actions.scss';
.dropdown {
  &__placeholder {
    display: none;
    .dropdown--open & {
      display: block
    }
  }
  &__selectables {
    vertical-align: middle;
    display: inline-block;
    .dropdown--open & {
      display: none
    }
    & + .dropdown__selectables {
      &:before {
        content: ",  ";
        margin-right: 2px;
      }
      &:after {
        content: "...";
        margin-left: -2px;
      } 
    }
  }
}
</style>


