
<template>
<form @submit.prevent="$emit('submit', query)">
    <re-input-container class="re-input-container__explore">
        <re-input placeholder="Search" v-model="query"></re-input>
        <svgicon name="search" width="20" height="44" @click="$emit('submit', query)"></svgicon>
    </re-input-container>
    </form>
</template>

<script>
import reInputContainer from '@/components/elements/reInputContainer/reInputContainer'
import reInput from '@/components/elements/reInputContainer/reInput'

export default {
  name: 'searchbar',
  data: () => ({
    loading: false
  }),
  props: ['query'],
  methods: {
    onCloseInput () {
      this.query = ''
      this.$emit('clear')
    }
  },
  components: {
    reInputContainer,
    reInput
  }
}
</script>

<style lang="scss" scoped>
</style>



