<template>
  <div id='classifierExplore'>
    <!-- biome logo -->
    <biome-logo :height=50 :width=240 class="biome-logo"></biome-logo> 
    <!-- search bar -->
    <searchbar :query="query" @submit="onQuery"></searchbar>
    <!-- filters list -->
    <filters-list
      :filters='filters'
      :filtersStatus='filtersStatus'
      @filterConfidenceApply='onFilterConfidenceApply' 
      @apply='onApply'
      @remove='onRemove'>
    </filters-list>
    <!-- show filters tags -->
    <filters-tags 
      :query="query" 
      :filtersStatus="filtersStatus" 
      @removeFilter="onRemoveFilter" 
      @clear-all="onClearAll" 
      @clear-query="onClear">
    </filters-tags>
    <!-- search bar & pagination -->
    <div class="grid">
      <transition name="fade">
      <div class="empty" v-if="results.total === 0">
          <h1 class="data-title">0 results found </h1>
          <svg v-svg symbol="empty-search" role="presentation" class="illustration" ></svg>
      </div>
        <search-results v-else
          :records=getResults 
          :feedbackItems=feedback.state
          :view="currentView" 
          :total-pages="getTotalPages" 
          :total=getTotal 
          :per-page="pageSize" 
          :current-page="currentPage"
          @changeAllFeedbackStatus="changeAllFeedbackStatus" 
          @pagechanged="onPageChange"
          @sort=onSortResults 
          @feedbackChanged=onRecordFeedbackChanged>
        </search-results>
      </transition>
      <!-- stadistics -->
      <sidebar
        v-if="results.total !== 0"
        :metrics="getMetrics" 
        :total-items=getTotal 
        :feedbackMetrics=feedback.metrics
        :view="currentView">
      </sidebar>
    </div>
  </div>
</template>
<script>
  import reButton from '@/components/elements/reButton/reButton'
  import reSwitch from '@/components/elements/reSwitch/reSwitch'
  import reInputContainer from '@/components/elements/reInputContainer/reInputContainer'
  import biomeLogo from '@/components/elements/logos/biomeLogo'
  import reInput from '@/components/elements/reInputContainer/reInput'

  import searchbar from './filters/SearchBar'
  import filtersList from './filters/FiltersList'
  import filtersTags from './filters/FiltersTags'
  import SearchResults from './SearchResults'
  import sidebar from './filters/SideBar'
  
  import Vue from 'vue'
  import VueVega from 'vue-vega'
  
  import elasticsearch from './common/elasticsearch'
  import elasticsearchDataWrapper from './common/elasticsearchDataWrapper'

  Vue.use(VueVega)

  let initFiltersStatus = () => ({})

  export default {
    name: 'explore-results-classifier',
    data: () => ({
      query: '',
      feedback: {
        metrics: {},
        state: {}
      },
      currentView: 'feedback',
      // filters tags
      filtersStatus: initFiltersStatus(),
      // current page
      pageSize: 10,
      currentPage: 1,
      sortBy: undefined,
      sortOrder: 'desc'
    }),
    watch: {
      feedbackMetrics (newValue) {
        this.$set(this.feedback, 'metrics', newValue)
      },
      getResults (newResults) {
        newResults.forEach(record => {
          this.$set(this.feedback.state, record._id, (record.feedback || {}).status)
        })
      }
    },
    props: {
      feedbackResults: {
        type: Object,
        default: () => ({
          aggregations: {}
        })
      },
      results: {
        type: Object,
        default: () => ({
          total: 0,
          items: [],
          aggregations: {}
        })
      },
      view: String
    },
    created () {
      this.currentView = this.view
      this.emitQueryUpdated()
    },
    methods: {
      onPageChange (page) {
        this.currentPage = page
        this.emitQueryUpdated()
      },
      onQuery (textQuery) {
        this.query = textQuery
        this.currentPage = 1
        this.emitQueryUpdated()
      },
      onClear () {
        this.query = ''
        this.currentPage = 1
        this.emitQueryUpdated()
      },
      onFilterConfidenceApply (filterId, value) {
        this.$set(this.filtersStatus, filterId, [value])
        this.currentPage = 1
        this.emitQueryUpdated()
      },
      onApply (filters) {
        this.updateActiveFilters(filters)
        this.currentPage = 1
        this.emitQueryUpdated()
      },
      onRemove (group, value) {
        this.filtersStatus[group] = []
        this.currentPage = 1
        this.emitQueryUpdated()
      },
      onRecordFeedbackChanged ({id, oldFeedback, newFeedback}) {
        let findAnnotationValue = (feedback, record) => {
          let classes = Object.keys(record.classes)
          switch (feedback) {
            case 'validated':
              return record.predicted
            default:
              return classes.find(cls => cls !== record.predicted)
          }
        }
        let updateFeedbackValues = (id, oldFeedback, newFeedback) => {
          this.$set(this.feedback.state, id, newFeedback)
          if (oldFeedback) {
            this.$set(this.feedback.metrics, oldFeedback, (this.feedback.metrics[oldFeedback] || 0) - 1)
          }
          this.$set(this.feedback.metrics, newFeedback, (this.feedback.metrics[newFeedback] || 0) + 1)
        }
        if (oldFeedback !== newFeedback) {
          let foundRecord = this.getResults.find(record => record._id === id)
          let feedbackValue = findAnnotationValue(newFeedback, foundRecord)
          this.$emit('recordFeedbackChanged', {
            id: id,
            oldFeedback: oldFeedback,
            feedback: newFeedback,
            feedbackValue: feedbackValue
          })
          updateFeedbackValues(id, oldFeedback, newFeedback)
        }
      },
      isActiveFilter (id) {
        return this.filtersStatus[id] && this.filtersStatus[id].length > 0
      },
      buildQuery () {
        let query = {
          keyword: this.query,
          filters: Object.values(this.filters).filter(filter => this.isActiveFilter(filter.id)),
          filtersStatus: this.filtersStatus,
          esOptions: {
            from: (this.currentPage * this.pageSize) - this.pageSize,
            size: this.pageSize
          },
          sortBy: this.sortBy,
          sortOrder: this.sortOrder
        }
        return elasticsearch.toESQuery(query, this.isExploreView)
      },
      onRemoveFilter (key, value) {
        this.filtersStatus[key] = this.filtersStatus[key].filter(v => v !== value)
        this.emitQueryUpdated()
      },
      changeAllFeedbackStatus (status) {
        this.getResults
        .filter(r => status !== this.feedback.state[r._id])
        .forEach(r => {
          let oldFeedback = this.feedback.state[r._id]
          this.onRecordFeedbackChanged({
            id: r._id,
            oldFeedback: oldFeedback,
            newFeedback: status
          })
        })
      },
      onClearAll () {
        this.query = ''
        this.filtersStatus = initFiltersStatus()
        this.emitQueryUpdated()
      },
      emitQueryUpdated () {
        if (this.isFeedbackView) {
          this.$emit('refreshFeedbackResults', elasticsearch.feedbackResultsQuery())
        }
        this.$emit('queryChanged', this.buildQuery())
      },
      updateActiveFilters (selectedFilters) {
        Object.values(selectedFilters).forEach(selectedFilter => {
          (selectedFilter.values || [])
          .filter(filterValue => filterValue.selected)
          .forEach(filterValue => {
            let filterStatus = this.filtersStatus[selectedFilter.id] || []
            filterStatus.push(filterValue.id)
            this.$set(this.filtersStatus, selectedFilter.id, filterStatus)
          })
        })
      },
      onSortResults (sortBy, sortOrder) {
        this.sortBy = sortBy
        this.sortOrder = sortOrder
        this.emitQueryUpdated()
      }
    },
    computed: {
      confidenceFilterId () {
        return (this.filters.confidence || {}).id
      },
      feedbackMetrics () {
        return elasticsearchDataWrapper.feedbackMetricsFromAggregation(this.feedbackResults.aggregations)
      },
      filters () {
        return elasticsearchDataWrapper.filtersFromAggregations(this.results.aggregations)
      },
      metrics () {
        return elasticsearchDataWrapper.metricsFromAggregation(this.results.aggregations)
      },
      isFeedbackView () {
        return this.currentView === 'feedback'
      },
      isExploreView () {
        return this.currentView === 'explore'
      },
      getTotal () {
        return this.results.total
      },
      getTotalPages () {
        return Math.round(this.getTotal / this.pageSize)
      },
      getMetrics () {
        return this.metrics
      },
      getResults () {
        return (this.results.items || [])
          .map(elasticsearchDataWrapper.mapESDocument2Record)
          .slice()
      }
    },
    components: {
      reSwitch,
      filtersList,
      filtersTags,
      searchbar,
      SearchResults,
      sidebar,
      reInputContainer,
      reInput,
      reButton,
      biomeLogo
    }
  }
</script>
<style lang="scss" scoped>
  @import 'src/assets/scss/pages/actions.scss';
  @import 'src/assets/scss/pages/empty.scss';
  .biome-logo {
    float: right;
    .re-dialog & {
      display: none;
    }
  }
</style>



